@import url(https://fonts.googleapis.com/css?family=Open+Sans);

.navbar {
    font-size: 18px;
    background-image: linear-gradient(260deg, #2376ae 0%, #c16ecf 100%);
    border: 1px solid rgba(0, 0, 0, 0.2);
    padding-bottom: 10px;
  }
  
  .main-nav {
    list-style-type: none;
    display: none;
  }
  
  .nav-links,
  .logo {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.7);
  }
  
  .main-nav li {
    text-align: center;
    margin: 15px auto;
  }
  
  .logo {
    display: inline-block;
    font-size: 22px;
    margin-top: 10px;
    margin-left: 20px;
  }
  
  .navbar-toggle {
    position: absolute;
    top: 10px;
    right: 20px;
    cursor: pointer;
    color: rgba(255, 255, 255, 0.8);
    font-size: 24px;
  }
  
  #chkToggle {
    display: none;
  }
  
  #chkToggle:checked + ul.main-nav {
    display: block;
  }
  
  @media screen and (min-width: 768px) {
    .navbar {
      display: flex;
      justify-content: space-between;
      padding-bottom: 0;
      height: 70px;
      align-items: center;
    }
  
    #chkToggle:checked + ul.main-nav {
      display: flex;
    }
  
    .main-nav {
      display: flex;
      margin-right: 30px;
      flex-direction: row;
      justify-content: flex-end;
    }
  
    .main-nav li {
      margin: 0;
    }
  
    .nav-links {
      margin-left: 40px;
    }
  
    .logo {
      margin-top: 0;
    }
  
    .navbar-toggle {
      display: none;
    }
  
    .logo:hover,
    .nav-links:hover {
      color: rgba(255, 255, 255, 1);
    }
  }
  
body {
	 background-color: #f5f6fd;
	 font-family: 'Open Sans', serif;
}
 .wrapper {
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 height: 100vh;
}
 .wrapper .container-search {
	 width: 100%;
	 display: flex;
	 flex-direction: column;
	 align-items: center;
	 max-width: 617px;
	 width: 100%;
	 box-sizing: border-box;
}
 .wrapper .container-search h1 {
	 color: white;
	 text-align: center;
	 color: #0000fa;
	 font-weight: bold;
     margin-bottom: 30px;
}
 .wrapper .container-search .container-input {
	 display: flex;
	 flex-direction: row;
	 width: 100%;
}
 .wrapper .container-search .container-input .search-term {
	 max-width: 585px;
	 width: 100%;
	 background-color: white;
	 border: 2px solid #0000fa;
	 border-right: none;
	 color: #0000fa;
	 padding: 5px 5px 5px 25px;
	 height: 54px;
	 border-radius: 50px 0 0 50px;
	 outline: none;
	 -webkit-appearance: none;
	 font-size: 20px;
}
 .wrapper .container-search .container-input .search-term::-webkit-calendar-picker-indicator {
	 display: none;
}
 .wrapper .container-search .container-input .search-term:focus {
	 color: #0000fa;
}
 .wrapper .container-search .container-input .search-term::-webkit-input-placeholder {
	 color: #a3a3ff;
	 font-weight: italic;
}
 .wrapper .container-search .container-input .search-term::placeholder {
	 color: #a3a3ff;
	 font-weight: italic;
}
 .wrapper .container-search .container-input .search-button {
	 width: 70px;
	 height: 54px;
	 background: white;
	 text-align: center;
	 color: #0000fa;
	 border-radius: 0 50px 50px 0;
	 border: 2px solid #0000fa;
	 cursor: pointer;
	 font-size: 25px;
	 border-left: none;
}
 .wrapper .container-search ul.search-history {
	 list-style-type: none;
	 padding: 0;
	 margin: 16px 0 0 0;
	 width: 100%;
}
 .wrapper .container-search ul.search-history:hover li {
	 opacity: 0.5;
}
 .wrapper .container-search ul.search-history li {
	 color: #0000fa;
	 font-size: 16px;
	 margin-bottom: 8px;
	 padding: 12px 24px;
	 display: flex;
	 flex-direction: row;
	 align-items: center;
	 position: relative;
	 transition: all 0.3s ease-in-out;
	 border-radius: 8px;
}
 .wrapper .container-search ul.search-history li:hover {
	 opacity: 1;
}
 .wrapper .container-search ul.search-history li span.text {
	 position: relative;
	 z-index: 3;
}
 .wrapper .container-search ul.search-history li span.text a {
	 text-decoration: none;
}
 .wrapper .container-search ul.search-history li a.link {
	 width: 100%;
	 height: 40px;
	 display: block;
	 position: absolute;
	 z-index: 2;
	 left: 0;
}
 .wrapper .container-search ul.search-history li:before, .wrapper .container-search ul.search-history li:after {
	 font-family: 'Font Awesome\ 5 Free';
	 font-weight: 900;
	 margin-right: 12px;
	 transition: all 0.3s ease-in-out;
}

 .wrapper .container-search ul.search-history li:after {
	 /* content: '\f30b'; */
	 opacity: 0;
	 position: absolute;
	 right: 16px;
}
 .wrapper .container-search ul.search-history li:hover {
	 background: rgba(0, 0, 250, .1);
}
 .wrapper .container-search ul.search-history li:hover:after {
	 opacity: 1;
}
 
.search-history > li > span{
    margin-left: 10px;
}
* {
    box-sizing: border-box;
  }
  
  html {
    font-size: 100%;
  }
  
  .profileBody{
      top:0;
      left:0;
      width: 100vw;
        padding: 0;
        margin: 0;
        min-height: 100vh;
        background: linear-gradient(transparent, rgba( 110, 110, 110, .3)), linear-gradient(100deg, rgba(250, 214, 195, .8) 30%, #B0EAE8 120%);
    
      
  }
  .sidebar_wrapper {
    width: 100%;
    max-width: 26rem;
  }
  
  .about-dev {
    width: 100%;
    max-width: 26rem;
    margin: auto;
    box-shadow: 2px 4px 2px -2px rgba(0, 0, 0, .3), -2px -4px 15px -2px rgba(0, 0, 0, .2);
    animation: profile_in 0.8s;
  }
  
  @keyframes profile_in {
    0% {
      opacity: 0;
      transform: translateY(30%);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  
  /* profile card header */
  
  .profile-card_header-container {
    max-width: 15rem;
    margin: auto;
  }
  
  .profile-card_header {
    background: #272727;
    border-left: 0.625rem solid #97ece1;
    padding: 1.5em 1.5em 1em;
    text-align: center;
  }
  
  
  /* profile picture */
  
  .profile-card_header-imgbox {
    display: block;
    width: 9.5rem;
    height: 9.5rem;
    overflow: hidden;
    border-radius: 50%;
    margin: auto;
    background: rgba(250, 214, 195, 1);
    border: 0.375rem solid rgba(250, 214, 195, 1);
  }
  
  .profile-card_header img {
    max-width: 100%;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
  }
  
  
  /* header heading */
  
  .profile-card_header h1 {
    color: #f3f3f3;
    font-size: 1.5rem;
    margin-top: 0.8em;
    font-family: 'Oswald', sans-serif;
    font-weight: normal;
    position: relative;
  }
  
  
  /* header text span */
  
  .profile-card_header h1 span {
    font-size: 1.2rem;
    font-weight: 300;
    display: block;
    color: rgba(220, 220, 220, .95);
    margin-top: 0.25em;
    padding-top: 0.25em;
    border-top: 0.075em solid rgba(250, 214, 195, 1);
  }
  
  
  /* about section */
  
  .profile-card_about {
    line-height: 1.5;
    background: #ededed;
    padding: 1.5em 2rem;
    color: #222;
    font-family: 'Lato', sans-serif;
  }
  
  .profile-card_about h2 {
    margin: 0;
    display: inline-block;
    color: #333;
    font-weight: normal;
    text-transform: uppercase;
    font-size: 1.3rem;
    position: relative;
    z-index: 2;
    vertical-align: middle;
  }
  
  .profile-card_about h2::before {
    content: '';
    position: absolute;
    width: 110%;
    /*  max-width: 13.8rem;
  */
    height: 1rem;
    background: #c6f1eb;
    left: -5px;
    top: 50%;
    z-index: -1;
  }
  
  .profile-card_about p {
    font-weight: 300;
  }
  
  .profile-card_footer {
    margin-top: 1.5em;
    text-align: right;
  }
  
  
  /* social row */
  
  .social-row {
    margin-right: 0.5em;
  }
  
  .paw-icon,
  .heart-icon {
    display: inline-block;
    margin-left: 0.5em;
    transition: transform 0.3s;
  }
  
  #pawi,
  #hearti {
    height: 1.8rem;
    width: 1.8rem;
    cursor: help;
    transition: fill 0.3s ease-in-out;
  }
  
  #pawi path,
  #hearti path {
    fill: #272727;
  }
  
  .paw-icon:hover,
  .heart-icon:hover {
    transform: scale(1.2);
  }
  
  
  /* back to profile link */
  
  .back-to-profile,
  .back-to-profile:visited {
    display: inline-block;
    padding-left: 0.5em;
    padding-bottom: 0.1em;
    color: #222;
    text-decoration: none;
    font-weight: bold;
    border-top: 0.3125rem solid rgba(0, 0, 0, 0);
    border-bottom: 0.3125rem solid #97ece1;
    transition: border 0.3s ease-in-out;
  }
  
  .back-to-profile::after {
    content: "\25b6";
    margin: 0 0.75em;
    color: #222;
  }
  
  .back-to-profile:hover,
  .back-to-profile:focus {
    border-top-color: #222;
    border-bottom-color: #fdc1a0;
  }
  
  @media screen and (max-width: 26em) {
    .side_wrapper {
      min-height: 100vh;
      background: #ededed;
    }
    .about-dev {
      box-shadow: none;
    }
  }
  
  @media screen and (min-width: 33em) {
    .side_wrapper {
      margin: 2rem auto;
    }
    .profile-card_header {
      padding: 1.5em 4em 1em;
    }
  }
  
  @media screen and (min-height: 46em) {
    .side_wrapper {
      width: 100%;
      max-width: 26rem;
      margin: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    .about-dev {
      max-width: 26rem;
    }
  }
